/* init */
* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Futura';
  font-size: 16px;
}

body {
  text-transform: uppercase;
}

a,
a:hover {
  text-decoration: none;
}

li {
  list-style: none;
}

li:before {
  content: "";
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin-right: .5rem;
  border: 2px solid #003164;
  border-radius: 50%;
}

button, input {
	outline: none;
	display: block;
	overflow: visible;
}

/* start */

/* .shareable setting */
.circle-sm {
  width: .75rem;
  height: .75rem;
  border: 1px solid #003164;
  border-radius: 50%;
}

.circle-lg {
  width: 3rem;
  height: 3rem;
  border: 2px solid #003164;
  border-radius: 50%;
}

.circle-mid {
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid #003164;
  border-radius: 50%;
}

.container {
  display: grid;
  grid-template-columns: 65% 35%;
  height: 100vh;
}

.main-panel {
  display: grid;
  grid-template-rows: 20vh 40vh 20vh;
  padding: 3rem 5rem;
  background-color: #FFEDF7;
}


.add-task {
  position: relative;
  width: 50%;
}

.add-task input {
  width: 100%;
  height: 2.5rem;
  padding: 0 20% 0 1rem;
  color: #FF4384;
  font-size: 1rem;
  font-weight: bold;
  font-style: italic;
  border: none;
  text-transform: uppercase;
}

.add-task input::placeholder {
  color: #FF4384;
  font-weight: bold;
  font-style: italic;
  text-transform: uppercase;
}

.add-task input ~ button {
  position: absolute;
  top: 0;
  right: -20%;
  border: none;
  background-color: transparent;
  color: #FF4384;
  font-size: 2rem;
}

.activity-task {
  display: grid;
  grid-template-areas: "main-decoration header header"
              "main-decoration sub-decoration none"
              "main main main";
  grid-template-rows: 1.5rem 1.5rem 25vh;
  grid-template-columns: 3rem .75rem auto;
  grid-column-gap: .75rem;
  grid-row-gap: .5rem;
  width: 65%;
}

.activity-task .main-decoration {
  grid-area: main-decoration;
}

.activity-task .sub-decoration {
  grid-area: sub-decoration;
  border: 1px solid #FF4384;
}

.activity-task h3 {
  grid-area: header;
  position: relative;
  color: #003164;
  font-size: 1.5rem;
}

.activity-task div {
  grid-area: main;
  text-align: center;
  color: #FF4384;
  font-size: 25vh;
  font-weight: 800;
}

.tasks-group {
  width: 60%;
}

.tasks-group ul li {
  display: grid;
  grid-template-columns: 1rem auto 1rem;
  grid-gap: .5rem;
  padding: .5rem 0;
  border-bottom: 2px solid rgba(0,49,100,.2);
  color: #003164;
  font-weight: bold;
}

.tasks-group ul li i {
  color: #003164;
}

.tasks-group a {
  color: #FF4384;
  display: flex;
  justify-content: flex-end;
  margin-top: .5rem;
}

.sub-panel {
  background-color: #003164;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  padding: 3rem 0;
}

.sub-panel li i {
  color: white;
  font-size: 2rem;
}

.sub-panel .logo {
  width: 100%;
}

.sub-panel .logo div {
  color: white;
  text-align: right;
  font-size: 1.5rem;
  font-weight: bold;
  transform: rotate(90deg);
  transform-origin: right top;
}

.sub-panel nav,
.sub-panel div {
  width: 80%;
}

.sub-panel nav ul {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end;
}

.sub-panel nav ul li {
  margin-bottom: 3rem;
}

.clock-wrapper {
  position: absolute;
  left: calc(65% - 225px);
  top: calc(50vh - 225px);
  width: 450px;
  height: 450px;
  border-radius: 50%;
  border: 4px solid #FF4384;
  background-color: transparent;
}

.clock-main {
  position: absolute;
  top: 10PX;
  left: 10px;
  width: 420px;
  height: 420px;
  border-radius: 50%;
  border: 4px solid #FF4384;
  background-color: #FF4384;
}

.clock-btn {
  position: relative;
  width: 6rem;
  height: 6rem;
  top: calc(50% - 3rem);
  left: calc(50% - 3rem);
  text-align: center;
  background-color: white;
  border-radius: 50%;
}

.material-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 6rem;
  font-size: 4rem;
  color: #FF4384;
  cursor: pointer;
}

.clock-decoration {
  position: absolute;
  content: "";
  right: -1.5rem;
  bottom: 0;
  width: .75rem;
  height: .75rem;
  background-color: white;
}

@media screen and (max-width: 1200px) {
  .activity-task div {
    font-size: 14vw;
    font-weight: 600;
  }

  .clock {
    left: calc(65% - 105px);
    top: calc(50vh - 105px);
    width: 210px;
    height: 210px;
  }

  .clock::before {
    top: -13PX;
    left: -13px;
    width: 230px;
    height: 230px;
    border: 3px solid #FF4384;
  }



}

@media screen and (max-width: 800px) {
  .container {
    display: flex;
    flex-flow: column nowrap;
    height: 100vh;
  }

  .main-panel {
    grid-template-rows: 15vh 50vh 25vh;
  }

  .add-task {
    width: 80%
  }

  .tasks-group {
    width: 100%;
  }

  .activity-task div {
    font-weight: 600;
    margin: 2rem;
  }


  .sub-panel {
    display: none;
  }

  .clock {
    top: 55%;
    width: 60px;
    height: 60px;
  }

  .clock-btn {
    width: 3rem;
    height: 3rem;
    top: calc(50% - 1.5rem);
    left: calc(50% - 1.5rem);
  }

  .clock::before {
    display: none;
  }

  .clock-btn::after  {
    display: none;
  }

  .clock-btn i {
    height: 3rem;
    font-size: 3rem;
  }

}

.main-theme {
  background-color: white;
}

.main-theme-font {
  color: white;
}

.sub-theme {
  background-color: #FF4384;
}
